function hidePopup() {
    const btns = document.querySelectorAll(".popup__btn");
    const popups = document.querySelectorAll(".popup");

    if (btns.length != 0) {
        btns.forEach(item => {
            item.addEventListener('click', function () {
                item.closest(".popup").classList.remove("active");
            });
        })
    }

    if (popups.length != 0) {
        popups.forEach(item => {
            item.addEventListener('click', function (event) {
                if(event.target == this)
                this.classList.remove("active");
            });
        })
    }
}

hidePopup();

function showPopup(id) {
    const popup = document.querySelector(id);

    if (popup) {
        popup.classList.add("active");
    }
}


function buttonClick(btn, id) {
    const btnOne = document.querySelector(btn);

    btnOne.addEventListener("click", function () {
        showPopup(id);
    })
}

buttonClick(".nav__btn--one", "#editor");
buttonClick(".nav__btn--two", "#congrat");
buttonClick(".nav__btn--three", "#answers");
buttonClick(".nav__btn--four", "#puzzle");

const items = document.querySelectorAll(".item");

if(items.length != 0) {
    items.forEach(item => {
        item.addEventListener('click', function() {
            showPopup("#editor");
        });
    });
}